import React from 'react'

const page = () => {
  return (
    <div className="min-h-screen bg-gray-50 text-gray-800 p-6">
      <h1 className="text-3xl font-bold mb-6">Dashboard Admin</h1>

      {/* Statistik Ringkas */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-10">
        <div className="bg-white shadow rounded-lg p-6">
          <h3 className="text-sm text-gray-500 mb-1">Total Produk</h3>
          <p className="text-2xl font-bold">120</p>
        </div>
        <div className="bg-white shadow rounded-lg p-6">
          <h3 className="text-sm text-gray-500 mb-1">Transaksi Hari Ini</h3>
          <p className="text-2xl font-bold">32</p>
        </div>
        <div className="bg-white shadow rounded-lg p-6">
          <h3 className="text-sm text-gray-500 mb-1">Pengguna Terdaftar</h3>
          <p className="text-2xl font-bold">785</p>
        </div>
      </div>

      {/* Tabel Data Produk */}
      <div className="bg-white shadow rounded-lg p-6">
        <h2 className="text-xl font-semibold mb-4">Daftar Produk Terbaru</h2>
        <table className="w-full text-sm text-left">
          <thead className="border-b border-gray-200 text-gray-600">
            <tr>
              <th className="py-2 px-4">Nama Produk</th>
              <th className="py-2 px-4">Kategori</th>
              <th className="py-2 px-4">Harga</th>
              <th className="py-2 px-4">Stok</th>
              <th className="py-2 px-4">Aksi</th>
            </tr>
          </thead>
          <tbody>
            {[1, 2, 3].map((id) => (
              <tr key={id} className="border-b border-gray-100 hover:bg-gray-50">
                <td className="py-2 px-4">Produk {id}</td>
                <td className="py-2 px-4">Elektronik</td>
                <td className="py-2 px-4">Rp {id * 100000}</td>
                <td className="py-2 px-4">{id * 5}</td>
                <td className="py-2 px-4">
                  <button className="text-blue-600 hover:underline text-xs">Edit</button> |{" "}
                  <button className="text-red-600 hover:underline text-xs">Hapus</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}

export default page